import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { DATE, formatDate, InputGroupInline, InputType, parseRefToDate, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { CARTA_PREPOSICAO_ENUM_VALUES } from '../../../../../models/enumerated/mfp/cartaPreposicaoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ResponsavelIS from '../../../../components/ger/responsavelIS/ResponsavelIS';
import CustomizacaoTermoIS from '../../../../components/mfp/customizacaoTermoIS/CustomizacaoTermoIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CartaPreposicaoRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './cartaPreposicaoRV';

const DEFAULT_VALUES: CartaPreposicaoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATE.value),
  dtInicialLabel: 'Data',
  dtInicialRequired: true,
  exibirConfiguracaoDadosEmpresa: false,
};

const CartaPreposicaoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<CartaPreposicaoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const [tipoPreposicao, setTipoPreposicao] = useState();
  const labelWidth = 200;

  const onChangeTipoPreposicao = tipoSelecionado => {
    if (tipoSelecionado === '1') {
      formProps.setValue('responsavelPreposto', { nome: 'NAO-SELECIONADO' });
      formProps.setValue('trabalhadorPreposto', undefined);
    } else {
      formProps.setValue('trabalhadorPreposto', { nome: 'NAO-SELECIONADO' });
      formProps.setValue('responsavelPreposto', undefined);
    }
    setTipoPreposicao(tipoSelecionado);
  };

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('dtInicial', formatDate(parseRefToDate(globalReducer.globalParameter.empresaMFP.referencia, 1)));
      }, 200);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = () => {
    const erros = [];

    const { trabalhadorPreposto, responsavelPreposto, trabalhadorDemitido } = formProps.getValues();

    if (trabalhadorPreposto && trabalhadorPreposto.matricula === trabalhadorDemitido.matricula) {
      erros.push('O Trabalhador Preposto deve ser diferente do Trabalhador Demitido.');
    }

    if (tipoPreposicao === '1' && !trabalhadorPreposto) {
      erros.push('Trabalhador Preposto - Campo não pode estar em branco.');
    } else if (tipoPreposicao === '2' && !responsavelPreposto) {
      erros.push('Responsável Preposto - Campo não pode estar em branco.');
    }

    return erros;
  };

  return (
    <WindowComponent
      title="Carta de Preposição"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <Row>
          <Col>
            <CustomizacaoTermoIS
              labelWidth={labelWidth}
              formProps={{ ...formProps, validation: { required: true } }}
              label="Termo"
              apenasCartaPreposicao={true}
              placeholder="Selecione o Termo"
              id="termo"
              isPrependLabel={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              id="tipoPreposicao"
              label="Tipo de Preposto"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: CARTA_PREPOSICAO_ENUM_VALUES,
                selectProps: { isClearable: true },
              }}
              type={InputType.DROP_DOWN}
              onChange={onChangeTipoPreposicao}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <VinculoIS
              labelWidth={labelWidth}
              id="trabalhadorDemitido"
              formProps={{ ...formProps, validation: { required: true } }}
              label="Trabalhador Demitido"
              placeholder="Selecione o Trabalhador Demitido"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
              tooltipProps={{ message: 'Trabalhador Demitido da Carta de Preposição.' }}
            />
          </Col>
        </Row>
        {tipoPreposicao === '1' ? (
          <Row>
            <Col>
              <VinculoIS
                labelWidth={labelWidth}
                id="trabalhadorPreposto"
                formProps={formProps}
                label="Trabalhador Preposto"
                placeholder="Selecione o Trabalhador"
                isPrependLabel={true}
                inputSearchProps={{ selectProps: { isMulti: false } }}
                tooltipProps={{ message: 'Trabalhador Preposto da Carta de Preposição.' }}
              />
            </Col>
          </Row>
        ) : null}
        {tipoPreposicao === '2' ? (
          <Row>
            <Col>
              <ResponsavelIS
                labelWidth={labelWidth}
                id="responsavelPreposto"
                formProps={formProps}
                label="Responsável Preposto"
                placeholder="Selecione o Responsável Preposto"
                inputSearchProps={{ selectProps: { isMulti: false } }}
                tooltipProps={{ message: 'Responsável Preposto da Carta de Preposição.' }}
              />
            </Col>
          </Row>
        ) : null}
      </ReportForm>
    </WindowComponent>
  );
};

export default CartaPreposicaoReportView;
